.header {
  padding-top: 11px;

  &__nav {
    display: none;
  }

  &__btn {
    display: none;
  }

  &__logo {
    text-align: center;
    display: block;
  }
}

@media (min-width: 768px) {
  .header {

    &__nav {
      display: inline-block;
      background-color: #fff;
    }

    &__item {
      display: inline-block;
      padding: 10px 25px;
      border-radius: 8px;
      transition: background 0.5s, box-shadow 0.5s;

      &:not(:first-child) {
        margin-left: 10px;
      }

      &:hover {
        background: #00C368;
        box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.4);
        padding: 10px 25px;

        .header__link {
          transition: color 0.5s;
          color: #fff;
        }
      }
    }

    &__link {
      color: #A6ABBD;
    }

    &__btn {
      display: block;
      transition: box-shadow 0.5s;
      float: right;
      margin-top: 16px;
      border-radius: 8px;
      font-size: 14px;

      &:hover {
        box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.4);
      }

      button {
        background: #00C368;
        border-radius: 8px;
        color: #fff;
        padding: 11px 25px;
      }
    }

    &__link {
      font-size: 14px;
    }

    &__logo {
      text-align: left;
      display: inline-block;
      vertical-align: middle;
    }
  }

  .active {
    background: #00C368;
    border-radius: 8px;
    color: #fff;
    padding: 10px 25px;
    transition: box-shadow 0.5s;

    &:hover {
      box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.4);
    }

    .header__link {
      color: #fff;
    }
  }
}

@media (min-width: 1024px) {
  .header {
    padding-top: 30px;
    padding-bottom: 37px;

    &__nav {
      margin-left: 20px;
    }

    &__item {
      display: inline-block;
      padding: 7px 24px;

      &:not(:first-child) {
        margin-left: 14px;
      }

      &:hover {
        padding: 7px 24px;
      }
    }

    &__btn button {
      padding: 11px 24px;
    }
  }

  .active {
    padding: 7px 24px;
  }
}

@media (min-width: 1080px) {
  .header {

    &__btn {
      margin-top: 29px;
      font-size: 16px;

      button {
        padding: 10px 35px;
      }
    }

    &__link {
      font-size: 16px;
    }
  }
}

@media (min-width: 1240px) {
  .header {

    &__item:not(:first-child) {
      margin-left: 40px;
    }

    &__nav {
      margin-left: 55px;
    }
  }
}
