.response {
  padding-top: 22px;

  &__title {
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: #2E3A59;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: -22px;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      width: 184px;
      height: 4px;
      background-color: #00C368;
      ;
    }
  }

  &__subtitle {
    font-size: 16px;
    line-height: 150%;
    text-align: center;
    color: #2E3A59;
  }

  &__left {
    margin-top: 75px;
  }

  &-big {

    &__item {
      background: #FFFFFF;
      box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.24);
      border-radius: 32px;
      padding: 80px 16px 16px;
      margin-top: 90px;
      margin-bottom: 38px;
      position: relative;
    }

    &__item-ql {
      fill: #E7EBF0;
      position: absolute;
      top: 24px;
      left: 16px;
    }

    &__item-qr {
      fill: #E7EBF0;
      transform: rotate(-180deg);
      position: absolute;
      top: 24px;
      right: 16px;
    }

    &__item-img {
      width: 97px;
      height: 97px;
      margin: 0 auto;
      position: absolute;
      left: 50%;
      top: -47px;
      transform: translateX(-50%);
    }

    &__item-text {
      font-size: 16px;
      line-height: 150%;
      text-align: center;
      color: #2E3A59;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      margin: 0;
    }

    &__item-author {
      font-weight: 700;
      text-align: center;
      font-size: 16px;
      line-height: 150%;
      color: #2E3A59;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      margin-top: 15px;
      margin-bottom: -5px;
    }
  }

  &__item {
    margin-bottom: 26px;

    &-img {
      width: 59px;
      height: 59px;
      margin-left: 10px;
    }

    &-text {
      margin: 0;
      font-size: 16px;
      line-height: 150%;
      color: #2E3A59;
    }

    &-author {
      font-size: 16px;
      line-height: 150%;
      color: #2E3A59;
      font-weight: 700;
      margin-top: 15px;
      margin-bottom: 28px;
    }
  }

  &__item--right {
    .response__item-desc {
      float: right;
      width: 69%;
      padding-top: 10px;
    }

    .response__item-img {
      float: left;
    }
  }

  &__item--left {
    .response__item-desc {
      float: left;
      width: 66%;
      padding-top: 10px;
    }

    .response__item-img {
      float: right;
    }
  }
}

.accordion {
  margin-top: 23px;
}

li.accordion-item {
  padding-top: 6px;
  padding-bottom: 7px;
}

.accordion-item:not(:last-child) {
  border-bottom: 1px solid #E7EBF0;
}

.accordion-thumb {
  cursor: pointer;
  position: relative;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.2;
  color: #2E3A59;
  margin: 0;
  padding: 0;
  padding-right: 20px;

  &::before {
    content: '';
    display: inline-block;
    width: 19px;
    height: 10px;
    vertical-align: middle;
    background-image: url(../img/arr-b.svg);
    transition: transform .2s ease-out;
    position: absolute;
    right: 0;
    top: 12%;
  }
}

.accordion-panel {
  display: none;
  font-size: 16px;
  line-height: 19px;
  color: #2E3A59;
  padding-right: 30px;
}

.accordion-item.is-active {
  .accordion-thumb {
    color: #00C368;
  }

  .accordion-thumb::before {
    transform: rotate(180deg);
  }
}

@media (min-width: 768px) {
  .accordion {
    margin-bottom: 90px;
  }

  .response-big__item {
    box-shadow: none;
    border-radius: 0;
    margin: 0;
    padding: 0;
    padding-right: 60px;
    margin-bottom: 100px;
  }

  .response-big__item-img {
    width: 187px;
    height: 187px;
    margin: 0;
    left: 0;
    top: 0;
    transform: none;
  }

  .response-big__item-desc {
    float: right;
    width: 69%;
    position: relative;
    padding-right: 35px;
    padding-left: 35px;
  }

  .response-big__item-text {
    text-align: start;
  }

  .response-big__item-author {
    text-align: start;
  }

  .response-big__item-ql {
    fill: #00C368;
    left: -30px;
    top: 17px;
  }

  .response-big__item-qr {
    fill: #00C368;
    bottom: 20px;
    top: inherit;
  }

  .response__item--right {
    float: right;
    width: 50%;
  }

  .response__item--left {
    float: left;
    width: 50%;
    padding-left: 40px;
  }

  .response__item-img {
    width: 97px;
    height: 97px;
  }

  .clear--size {
    clear: none;
  }
}

@media (min-width: 1024px) {}

@media (min-width: 1080px) {}

@media (min-width: 1240px) {
  .response {
    position: relative;
    padding-top: 84px;
  }

  .overaly {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    background-color: #F5F5F5;
    padding-top: 90px;
  }

  .response-big__item {
    background: none;
    margin-bottom: 130px;
  }

  .response__right {
    width: 40%;
    float: right;
    margin-bottom: 100px;
  }

  .response__left {
    float: left;
    width: 55%;
    margin-top: 0;
  }

  .response__title {
    font-size: 40px;
    line-height: 47px;
    padding: 0 50px;
  }

  .response-big__item-img {
    width: 210px;
    left: 0px;
    top: 11px;
  }

  .response-big__item-desc {
    width: 63%;
    padding-left: 55px;
    padding-right: 15px;
    padding-top: 12px;
  }

  .response-big__item-ql {
    left: 0;
  }

  .response-big__item-qr {
    bottom: -12px;
  }

  .accordion-thumb {
    font-size: 24px;
    line-height: 28px;
    padding-bottom: 4px;
    padding-top: 7px;
  }

  .accordion {
    max-width: 615px;
  }

  li.accordion-item {
    position: relative;

    &:before {
      content: "";
      position: absolute;
      display: block;
      left: -90px;
      top: 8px;
      width: 40px;
      height: 40px;
    }
  }

  li.accordion-item:nth-child(1):before {
    background-image: url(../img/sprite.svg#stability);
  }

  li.accordion-item:nth-child(2):before {
    background-image: url(../img/sprite.svg#led);
  }

  li.accordion-item:nth-child(3):before {
    background-image: url(../img/sprite.svg#lights);
  }

  li.accordion-item:nth-child(4):before {
    background-image: url(../img/sprite.svg#splitter);
  }

  li.accordion-item:nth-child(5):before {
    background-image: url(../img/sprite.svg#power);
  }

  .accordion-thumb::before {
    top: 47%;
  }

  .response__item-img {
    width: 111px;
    height: 97px;
  }
}

@media (min-width: 1850px) {
  .response-big__item-img {
    left: 120px;
  }
}
