.info {
  padding-top: 60px;
  padding-bottom: 150px;
  position: relative;
  background: none;

  &__bg-mob {
    width: 270px;
    margin: 0 auto;
  }

  &__title {
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    text-transform: uppercase;
    color: #2E3A59;
    margin: 0;
    margin-top: -21px;
    margin-bottom: 37px;
  }
}

.slick-list {
  overflow: hidden;
}

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  user-select: none;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-initialized .slick-slide {
  display: block;
}

.info-slider {
  position: relative;
  padding: 10px;

  .slick-arrow {
    position: absolute;
    bottom: -72px;
    z-index: 10;
    background: #000;
    font-size: 0;
    width: 10px;
    height: 19px;
    border: none;
    background: transparent;
    cursor: pointer;

    &.slick-prev {
      left: 0;
      background: url(../img/arr-l.svg) 0 0 no-repeat;
    }

    &.slick-next {
      right: 0;
      background: url(../img/arr-r.svg) 0 0 no-repeat;
    }
  }

  .slick-dots {
    position: absolute;
    bottom: -65px;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;

    .slick-active button {
      background-color: #A6ABBD;
      margin: 0 10px;
    }

    li {
      position: relative;
      display: inline-block;
      list-style: none;
      margin: 0 6px;
    }

    button {
      position: relative;
      display: inline-block;
      font-size: 0;
      width: 8px;
      height: 8px;
      background-color: #E7EBF0;
      border-radius: 100%;
      cursor: pointer;
      border: none;
      padding: 0;
    }
  }

  &__item {
    width: 100px;
  }

  &__icon {
    width: 85px;
    height: 85px;
    margin: 0 auto;



    // svg {
    //   // fill: none;
    //   --color: transparent;
    //   transition: color 0.5s;

    //   &:hover {
    //     // fill: #00C368;
    //     --color: #00C368;
    //   }

    //   use {
    //     transition: 0.5s;
    //     fill: var(--color);
    //   }
    // }

    svg {
      stroke: #2E3A59;
      transition: 0.5s;

      &:hover {
        stroke: #00C368;
        stroke-width: 1;
        filter: url(./../img/sprite.svg#mic#filter2);
      }
    }

  }

  &__text {
    font-size: 16px;
    line-height: 19px;
    color: #2E3A59;
    width: 180px;
    text-align: center;
    margin: 0 auto;
    padding-top: 11px;
  }
}

.info__items {
  display: none;
}


#filter2 {
  display: none;
}

@media (min-width: 768px) {}

@media (min-width: 1024px) {

  .info {
    background: linear-gradient(111.04deg, #E2E2E2 0.39%, rgba(255, 255, 255, 0) 86.46%);
    position: relative;
    padding-top: 115px;
    padding-bottom: 85px;

    &::after {
      content: "";
      position: absolute;
      bottom: 90px;
      left: 60px;
      display: block;
      width: 350px;
      height: 350px;
      background-image: url(../img/bg-left.png);
      background-size: cover;
    }

    &::before {
      content: "";
      position: absolute;
      top: 75px;
      right: 0;
      display: block;
      width: 495px;
      height: 520px;
      background-image: url(../img/bg-right.png);
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  .info-slider {
    display: none;
  }

  .info__container {
    position: relative;
  }

  .info__bg-mob {
    display: none;
  }

  .info__bg {
    width: 440px;
  }

  .info {

    &__title {
      font-size: 40px;
      line-height: 47px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        top: -22px;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        width: 184px;
        height: 4px;
        background-color: #00C368;
        ;
      }
    }

    &__items {
      display: block;
      padding-top: 30px;
      z-index: 5;
      position: relative;
    }

    &__items-row {
      display: block;
      max-width: 850px;
      margin: 0 auto;
      text-align: center;

      &:first-child {
        margin-bottom: 50px;
      }

      &:nth-child(odd) {
        .info__items-item:not(:first-child) {
          margin-left: 100px;
        }
      }
    }

    &__items-bg {
      max-width: 450px;
    }

    &__items-item {
      display: inline-block;
      vertical-align: middle;
    }
  }

}

@media (min-width: 1080px) {}

@media (min-width: 1240px) {}
